.header__logo {
  background-image: url(../../../images/header-logo.svg);
  background-repeat: no-repeat;
  background-size: cover;
}

@media screen and (max-width:767px){
  .header__logo {
    width: 103.74px;
    height: 24.4px;
  }
}

@media screen and (min-width:768px){
  .header__logo {
    width: 142px;
    height: 33px;
  }
}
