.auth-form__submit-button {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  width: 100%;
  padding: 14px 0;
  margin: 0;
  border: none;
  border-radius: 2px;
  box-sizing: border-box;
  cursor: pointer;
  background-color: #fff;
  opacity: 1;
  transition: opacity 0.3s ease;
}

.auth-form__submit-button:hover {
  opacity: 0.85;
}
